const characterSvc = require('./characterService');

module.exports = class stateSvc {
  getDefaultState() {
    const defaultState = {
        characterInfo: {
            characterClass: 'bladeswarm',
            gold: 0,
            totalXp: 0,
            level: 1,
            retirements: 0,
            items: [],
            checks: 0,
            availablePerks: 0,
            allPerkCount: 0,
            perks: [],
            modifierDeck: {
                bless: 0,
                curse: 0,
                plusZero: 6,
                plusOne: 5,
                minusOne: 5,
                plusTwo: 1,
                minusTwo: 1,
                timesTwo: 1,
                miss: 1,
            }
        },
        scenarioInfo: {
            prosperity: 0,
            difficultyLevel: 1,
            retiredCharacters: 0,
        },
        currentStats: {
            xp: 0,
            hp: 10,
            maxHp: 10,
            coins: 0,
            activeItems: [],
            usedItems: [],
            spentItems: [],
            shuffledDeck: [],
            promptToShuffle: false,
            discard: [],
            lastDraw: [],
            character: undefined,
        }
    };
    const thisCharacterSvc = new characterSvc();
    defaultState.currentStats.character = thisCharacterSvc.loadCharacter(defaultState.characterInfo.characterClass);
    return defaultState;
  }

  loadState() {
    const defaultState = this.getDefaultState();
    var localState = defaultState;
    // for all items in state
    for (let key in defaultState) {
      // if the key exists in localStorage
      if (localStorage.hasOwnProperty(key)) {
        // get the key's value from localStorage
        let value = localStorage.getItem(key);
        // parse the localStorage string and setState
        try {
          value = JSON.parse(value);
          localState[key] = value;
        } catch (e) {
          // handle empty string
          localState[key] = value;
        }
      }
    }
    return localState;
  }

  clearState() {
    const defaultState = this.getDefaultState();
    for (let key in defaultState) {
      // if the key exists in localStorage
      if (localStorage.hasOwnProperty(key)) {
        localStorage.removeItem(key);
      }
    }
    return defaultState
  }

  saveState(state) {
    // for every item in React state
    for (let key in state) {
      // save to localStorage
      localStorage.setItem(key, JSON.stringify(state[key]));
    }
  }
}



