import React from 'react';
import {
  Button,
} from 'react-bootstrap';
import PropTypes from 'prop-types';
import "./Options.css";

class Options extends React.Component {
    updateCharacter(a) {
       return this.props.onUpdateCharacter(a);
    }

    resetDeck() {
      const removes = ['bless', 'curse', 'scenarioMinusOne'];
      var newModDeck = this.props.characterInfo.modifierDeck;
      removes.forEach( (type) => {
          newModDeck[type] = 0;
      });

      this.updateCharacter({
        'modifierDeck' : newModDeck,
        'xp' : 0,
        'hp' : this.props.currentStats.maxHp,
        'promptToShuffle' : false
      });
      this.clearDiscard();
    }

    clearDiscard() {
      this.updateCharacter({
        'discard': [],
        'lastDraw': []
      });
    }

    clearState() {
      this.updateCharacter({
        'resetEverything' : 1
      });
    }



  render() {
    return (
        <div className = 'options'>
        <Button bsSize="lg" bsStyle="danger" onClick={() => {this.resetDeck()}}>Restart Scenario</Button><br /><br />
        <Button bsSize="lg" bsStyle="danger" onClick={() => {this.clearDiscard()}}>Clear Discard</Button><br /><br />
        <Button bsSize="lg" bsStyle="danger" onClick={() => {this.clearState()}}>Clear Character</Button>
        </div>
    );
  }
}

Options.propTypes = {
  onUpdateCharacter: PropTypes.func.isRequired,
  currentStats: PropTypes.object.isRequired,
  characterInfo: PropTypes.object.isRequired,
};

export default Options;



